.typecont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.detailcont {
    display: flex;
    flex-direction: row;
}

.cont {
    display: flex;
    flex-direction: row;

}

.testcont {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.correct {
    color: green;
}

.btns {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    width: 100%;
}

.leftcont {
    height: 320px;
    width: 470px;
    background-color: rgb(139, 223, 235);
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
}

p {
    padding-left: 10px;
}

.rightcont {
    height: 330px;
    width: 470px;
    background-color: rgb(85, 83, 83);
    flex-direction: column;
    justify-content: space-between;

}

textarea {
    height: 98%;
    width: 100%;
    outline: none;
}

.incorrect {
    color: red;

}

#reset {
    height: 30px;
    width: 50px;
    margin-top: 70px;
}

.card {
    display: flex;
    border: 1px solid;
    padding: 10px;
    width: 383px;
    height: 30px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;

}

.box1 {
    display: flex;
    flex-direction: row;
}

.card:hover {
    box-shadow: 0px 5px 7px rgb(29, 28, 28);
}

.timer {
    margin-top: 20px;
}

.text {
    margin-bottom: 20px;
}

@media (max-width:768px) {
    .card {
        width: 102px;
        height: 20px;
        padding: 15px 10px
    }

    .cont {

        flex-direction: column;

    }

    .leftcont {
        height: 230px;
        width: 350px;
        font-size: 16px;

    }

    .rightcont {
        height: 230px;
        width: 361px;

    }

    textarea {
        height: 100%;
        width: 98%;
    }
}