.test-container {
    color: black;
    background-image: linear-gradient(to bottom right, #9fa0a0, #fdffff);
    display: flex;
    margin-bottom: 30px;
    width: 90%;
    height: 500px;
    margin-top: 30px;
    flex-direction: column;
    align-items: center;

    flex-wrap: wrap;

}

@media (max-width:768px) {
    .test-container {
        width: 95%;
        height: 655px;
        margin-top: 30px;
    }
}